// Typy reimov prehrvania
export type PlayMode = 'continuous' | 'daily' | 'specificDays';

// truktra pre plnovanie prehrvania
export interface Schedule {
  mode: PlayMode;
  startTime?: string; // formt HH:MM
  endTime?: string;   // formt HH:MM
  days?: number[];    // 0 = Nedea, 1 = Pondelok, ..., 6 = Sobota
}

// truktra projektu
export interface Project {
  id: number;
  name: string;
  schedule: Schedule;
}

/**
 * Funkcia na zistenie, i sa m projekt prehrva v aktulnom ase
 * @param project Projekt na kontrolu
 * @param currentDate Aktulny dtum a as (predvolene aktulny as)
 * @returns Boolean i sa m projekt prehrva
 */
export function shouldPlayProject(project: Project, currentDate: Date = new Date()): boolean {
  const { schedule } = project;
  
  // Reim bez plnovania - vdy sa prehrva
  if (schedule.mode === 'continuous') {
    return true;
  }
  
  // Kontrola asu
  if (schedule.startTime && schedule.endTime) {
    const currentTime = currentDate.getHours() * 60 + currentDate.getMinutes();
    const [startHours, startMinutes] = schedule.startTime.split(':').map(Number);
    const [endHours, endMinutes] = schedule.endTime.split(':').map(Number);
    
    const startTimeMinutes = startHours * 60 + startMinutes;
    const endTimeMinutes = endHours * 60 + endMinutes;
    
    // Ak aktulny as nie je v rozmedz zaiatku a konca, projekt sa neprehrva
    if (currentTime < startTimeMinutes || currentTime > endTimeMinutes) {
      return false;
    }
  }
  
  // Kontrola dn v tdni pre reim specificDays
  if (schedule.mode === 'specificDays' && schedule.days) {
    const currentDay = currentDate.getDay(); // 0 = Nedea, 1 = Pondelok, ...
    return schedule.days.includes(currentDay);
  }
  
  // Pre daily reim sta len kontrola asu, ktor sme u urobili vyie
  return schedule.mode === 'daily';
}

/**
 * Funkcia na zskanie asu zaiatku a konca prehrvania projektu
 * @param project Projekt
 * @returns Objekt obsahujci zaiaton a koncov as, alebo null ak projekt nem asov obmedzenie
 */
export function getProjectPlaytime(project: Project): { startTime: string; endTime: string } | null {
  const { schedule } = project;
  
  if (schedule.mode === 'continuous') {
    return null; // Projekt nem asov obmedzenie
  }
  
  if (schedule.startTime && schedule.endTime) {
    return {
      startTime: schedule.startTime,
      endTime: schedule.endTime
    };
  }
  
  return null;
}

/**
 * Funkcia na zistenie, ktor projekt sa m prehrva v aktulnom ase
 * @param projects Zoznam vetkch projektov
 * @param currentDate Aktulny dtum a as (predvolene aktulny as)
 * @returns Projekt, ktor sa m prehrva alebo undefined ak sa nem prehrva iadny
 */
export function getCurrentProject(projects: Project[], currentDate: Date = new Date()): Project | undefined {
  return projects.find(project => shouldPlayProject(project, currentDate));
}