<script setup lang="ts">
import { ref, defineEmits } from 'vue'
import type { Project, PlayMode } from '../types'

const emits = defineEmits(['addProject'])
const name = ref('')
const mode = ref<PlayMode>('continuous')
const startTime = ref('')
const endTime = ref('')
const days = ref<number[]>([])

const addProject = () => {
  // Validcia
  if (name.value.trim() === '') {
    alert('Zadajte nzov projektu')
    return
  }
  
  if (mode.value !== 'continuous' && (!startTime.value || !endTime.value)) {
    alert('Zadajte as zaiatku a konca prehrvania')
    return
  }
  
  if (mode.value === 'specificDays' && days.value.length === 0) {
    alert('Vyberte aspo jeden de na prehrvanie')
    return
  }
  
  const newProject: Project = {
    id: Date.now(),
    name: name.value,
    schedule: {
      mode: mode.value,
      startTime: mode.value !== 'continuous' ? startTime.value : undefined,
      endTime: mode.value !== 'continuous' ? endTime.value : undefined,
      days: mode.value === 'specificDays' ? days.value : undefined
    }
  }
  
  emits('addProject', newProject)
  
  // Vyistenie formulra
  name.value = ''
  mode.value = 'continuous'
  startTime.value = ''
  endTime.value = ''
  days.value = []
}
</script>

<template>
  <form @submit.prevent="addProject" class="project-form">
    <div class="form-group">
      <label for="project-name">Nzov projektu</label>
      <input 
        id="project-name"
        v-model="name" 
        placeholder="Zadajte nzov projektu" 
        required 
      />
    </div>
    
    <div class="form-group">
      <label for="play-mode">Reim prehrvania</label>
      <select id="play-mode" v-model="mode">
        <option value="continuous">Bez plnovania (nepretrit)</option>
        <option value="daily">Kadodenn prehrvanie</option>
        <option value="specificDays">Prehrvanie v uren dni</option>
      </select>
    </div>
    
    <div v-if="mode !== 'continuous'" class="form-group time-inputs">
      <div>
        <label for="start-time">as zaiatku</label>
        <input 
          id="start-time"
          type="time" 
          v-model="startTime" 
          required 
        />
      </div>
      <div>
        <label for="end-time">as konca</label>
        <input 
          id="end-time"
          type="time" 
          v-model="endTime" 
          required 
        />
      </div>
    </div>
    
    <div v-if="mode === 'specificDays'" class="form-group days-selection">
      <p>Vyberte dni v tdni:</p>
      <div class="days-checkboxes">
        <label v-for="(dayName, index) in ['Nedea', 'Pondelok', 'Utorok', 'Streda', 'tvrtok', 'Piatok', 'Sobota']" 
               :key="index" 
               class="day-checkbox">
          <input 
            type="checkbox" 
            :value="index" 
            v-model="days" 
          />
          {{ dayName }}
        </label>
      </div>
    </div>
    
    <button type="submit" class="submit-button">Prida projekt</button>
  </form>
</template>

